.page1-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
    background-size: cover;
}

.start-button, .claim-button {
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    background-color: rgba(108, 118, 124, 0.5); 
    color: white;
    cursor: pointer;
    border-radius: 20px;
}

.timer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.timer-text {
    font-size: 18px;
    color: #ffffff;
    padding: 10px 20px;
    background-color: rgba(108, 118, 124, 0.5); 
    border-radius: 20px;
}

.coins-text {
    font-size: 20px;
    color: #ffffff;
    margin-top: 10px;
}
