.progress-container {
    position: relative;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-end; 
    align-items: center;
    background-image: url('../../../../public/assets/main_screen.png'); 
    background-size: cover; 
    background-position: center; 
    padding: 20px;
}

.start-button {
    font-size: 1.5rem;
    padding: 10px 20px;
    align-items: center;
    cursor: pointer;
}


.coins-counter {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 1.5rem;
    color: #fdfdfd;
}

.page-switcher {
    display: flex;
    justify-content: center;
    margin-bottom: 20px; 
    width: 100%; 
    position: absolute;
    bottom: 20px; 
}

.page-button {
    font-size: 1rem;
    margin: 0 10px;
    padding: 10px 20px;
    cursor: pointer;
    background-color: #ddd;
    border: none;
    border-radius: 5px;
}

.page-button.active {
    background-color: #333;
    color: white;
}

.content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: #eeeeee;
}
